/* global */
*,
::after,
::before {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container,
.catalogue {
  text-align: center;
}

.catalogue {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 3fr));
  gap: 2rem;
  margin: 2rem;
}

h1 {
  text-align: center;
  font-size: 3rem;
  margin: 4rem 0 0 0;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.65rem;
}

h4 {
  font-size: 1.35rem;
}

p {
  text-align: center;
}

img {
  max-width: 100%;
}

/* sections */

.title h1 {
  margin-bottom: 1rem;
}

.card {
  width: 300px;
  display: flex;
  position: relative;
  min-height: 1px;
}

.card-title {
  margin: 0.25rem 0;
  text-shadow: 1px 1px #002266;
}

.card-caption {
  position: absolute;
  width: 300px;
  bottom: 0;
  border-bottom-left-radius: 0.9rem;
  border-bottom-right-radius: 0.9rem;
  background-color: rgba(108, 108, 108, 0.67);
  box-shadow: 0 -7px 25px -8px rgb(0 0 0 / 75%);
}

.card-caption p {
  margin-top: 0rem;
}

img {
  transition: all 0.2s ease-in-out;
  border-radius: 0.9rem;
}

img:hover {
  transform: scale(1.1);
}
